
const { __ } = wp.i18n;
const { compose } = wp.compose;
const { withSelect, withDispatch } = wp.data;
const { PluginDocumentSettingPanel } = wp.editPost;
const { ToggleControl, TextControl, PanelRow} = wp.components;
 
const ME_Meta_Plugin = ( { postType, postMeta, setPostMeta} ) => {

	if ( 'mole_event' !== postType ) return null;

    if (postMeta._me_date == '' || postMeta._me_time == '' || postMeta._me_location == '') {
        wp.data.dispatch( 'core/notices' ).createInfoNotice( 
            'Mole-heads up! Great work, you have a brand new event now, but some basic information is still missing! How about to fill some in the right panel?', 
            { id: 'META_NOTICE', isDismissible: true} );
    }

	return( 
        <div>    
        {  postMeta._me_recurring_toggle ? (

            <PluginDocumentSettingPanel title={ __( 'Mole events info', 'mole-events') } icon="calendar" initialOpen="true">
                <PanelRow>
                    <ToggleControl
                        label={ __( 'Recurring event', 'mole-events' ) }
                        onChange={ ( value ) => setPostMeta( { _me_recurring_toggle: value } ) }
                        checked={ postMeta._me_recurring_toggle }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Recurring every', 'mole-events' ) }
                        value={ postMeta._me_recurring }
                        type="text"
                        onChange={ ( value ) => setPostMeta( { _me_recurring: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Starting from', 'mole-events' ) }
                        value={ postMeta._me_date }
                        type="date"
                        onChange={ ( value ) => setPostMeta( { _me_date: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Time', 'mole-events' ) }
                        value={ postMeta._me_time }
                        type="time"
                        onChange={ ( value ) => setPostMeta( { _me_time: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Time to', 'mole-events' ) }
                        value={ postMeta._me_time_to }
                        type="time"
                        onChange={ ( value ) => setPostMeta( { _me_time_to: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Location', 'mole-events' ) }
                        value={ postMeta._me_location }
                        type="text"
                        onChange={ ( value ) => setPostMeta( { _me_location: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Price', 'mole-events' ) }
                        value={ postMeta._me_price }
                        type="number"
                        step="any"
                        onChange={ ( value ) => setPostMeta( { _me_price: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Currency', 'mole-events' ) }
                        value={ postMeta._me_price_currency }
                        type="text"
                        maxlength="3"
                        onChange={ ( value ) => setPostMeta( { _me_price_currency: value } ) }
                    />
                </PanelRow>
            </PluginDocumentSettingPanel>         
        ) : (
            <PluginDocumentSettingPanel title={ __( 'Mole events info', 'mole-events') } icon="calendar" initialOpen="true">
                <PanelRow>
                    <ToggleControl
                        label={ __( 'Recurring event', 'mole-events' ) }
                        onChange={ ( value ) => setPostMeta( { _me_recurring_toggle: value } ) }
                        checked={ postMeta._me_recurring_toggle }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Date', 'mole-events' ) }
                        value={ postMeta._me_date }
                        type="date"
                        onChange={ ( value ) => setPostMeta( { _me_date: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Time from', 'mole-events' ) }
                        value={ postMeta._me_time }
                        type="time"
                        onChange={ ( value ) => setPostMeta( { _me_time: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Time to', 'mole-events' ) }
                        value={ postMeta._me_time_to }
                        type="time"
                        onChange={ ( value ) => setPostMeta( { _me_time_to: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Location', 'mole-events' ) }
                        value={ postMeta._me_location }
                        type="text"
                        onChange={ ( value ) => setPostMeta( { _me_location: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Price', 'mole-events' ) }
                        value={ postMeta._me_price }
                        type="number"
                        step="any"
                        onChange={ ( value ) => setPostMeta( { _me_price: value } ) }
                    />
                </PanelRow>
                <PanelRow>
                    <TextControl
                        label={ __( 'Currency', 'mole-events' ) }
                        value={ postMeta._me_price_currency }
                        type="text"
                        maxlength="3"
                        onChange={ ( value ) => setPostMeta( { _me_price_currency: value } ) }
                    />
                </PanelRow>
            </PluginDocumentSettingPanel>
        )}
        </div>   
    );
}
 
export default compose( [
	withSelect( ( select ) => {		

		return {
			postMeta: select( 'core/editor' ).getEditedPostAttribute( 'meta' ),
			postType: select( 'core/editor' ).getCurrentPostType(),
		};
	} ),

	withDispatch( ( dispatch ) => {    

        return {
            setPostMeta( newMeta ) {       
                dispatch( 'core/editor' ).editPost( { meta: newMeta } );
                dispatch( 'core/notices' ).removeNotice('META_NOTICE');     
            }
		};
	} )
] )( ME_Meta_Plugin );